:root {
  --margin-base: 0.8%;
  --gap-base: 0.8%;
  --button-height: 2rem;
  --header-btn-font-size: 1rem;
  --header-btn-icon-size: 1rem;
}

.main-container {
  display: flex;
  flex-direction: row;
  height: 94vh;
  gap: var(--gap-base);
  margin-inline: var(--margin-base);
}

.main-container > div {
  margin-block: var(--margin-base);
}

body {
  height: 100vh;
  background-color: #f5f5f5;
}

#root {
  height: 100%;
}

.header {
  display: flex;
  align-items: center;
  height: 6vh;
  justify-content: space-between;
  background-color: #fff;
}

.header-logo {
  width: 30px;
  height: 30px;
  margin-right: 10px;
}

.header-title {
  font-size: 24px;
  color: #242424;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  font-family: "Segoe UI";
  margin-left: 0.5rem;
  font-size: 1.1rem;
}

.header-title span {
  font-weight: 400;
}

.left-section {
  position: relative;
  margin-block-end: 0.25% !important;
}

.header-left-section {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 1%;
}

.header-right-section {
  margin-right: 1%;
  display: flex;
  gap: 0.25rem;
}

.header button {
  height: var(--button-height) !important;
  font-size: var(--header-btn-font-size) !important;
}

.header button > span > svg {
  width: var(--header-btn-icon-size);
  height: var(--header-btn-icon-size);
}

/* Large screens (≥992px) */
@media (min-width: 992px) {
  .header-title {
    font-size: 0.7rem;
  }
  :root {
    --button-height: 1.3rem;
    --header-btn-font-size: 0.6rem;
    --header-btn-icon-size: 1rem;
  }
}

/* Extra Large screens (≥1200px) */
@media (min-width: 1200px) {
  .header-title {
    font-size: 0.9rem;
  }
  :root {
    --button-height: 1.5rem;
    --header-btn-font-size: 0.7rem;
    --header-btn-icon-size: 1.1rem;
  }
}

/* Extra Extra Large screens (≥1400px) */
@media (min-width: 1400px) {
  .header-title {
    font-size: 1.1rem;
  }
  :root {
    --button-height: 2rem;
    --header-btn-font-size: 0.8rem;
    --header-btn-icon-size: 1.2rem;
  }
}

/* Very Extra Large screens (≥1600px) */
@media (min-width: 1600px) {
  .header-title {
    font-size: 1.3rem;
  }
  :root {
    --button-height: 2.2rem;
    --header-btn-font-size: 1rem;
    --header-btn-icon-size: 1.4rem;
  }
}
